<template>
    <div class="item-icon">
        <span class="item" :style="`background:url(${cover}) -${icon_x}px -${icon_y}px`"></span>
    </div>
</template>
<script>
import { ApiHttpClient } from "@/globals";
export default {
    name:'npc-icon',
    props:{
        IconId:{
            type: Number
        }
    },
    data(){
        return{
            cover:`${ApiHttpClient.defaults.resourceURL}/dds/repute_npc.png`,
        }
    },
    computed:{
        icon_x(){
            let column = (this.IconId) % 8;
            const UNIT_SIZE = 64;
            return Math.max(UNIT_SIZE * column, 0);
        },
        icon_y(){
            let row = Math.floor(this.IconId / 8);
            const UNIT_SIZE = 64;
            return UNIT_SIZE * row;
        }
    },
    methods:{
        getIcon(pageIdx){
            let row = Math.floor(pageIdx / 8);
            let column = pageIdx % 8;
            const UNIT_SIZE = 64;

            let ret = {
                x: Math.max(UNIT_SIZE * column, 0),
                y: UNIT_SIZE * row,
            };
            return ret;
        },
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.item-icon {
    width: 64px;
    height: 64px;
    position: relative;
    display: inline-block;

    .item {
        box-shadow: 0px 0px 5px 3px rgb(84 236 65 / 0.72);
        border:1px solid #0befcf;
        position: absolute;
        top: 1px;
        .left(1px);
        .right(0);
        bottom: 0;
    }
}
</style>