<template>
    <div>
        <h1 class="divided">公会任务</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div class="stats">
                <div style="float:right;">
                    请选择公会任务阶段：
                    <selector @change="watchChange" :options="result.grades" :target="grade"></selector>
                    <!-- <select @change="watchChange" v-model="grade">
                        <option value="" selected disabled hidden></option>
                        <option v-for="(f,k) in result.grades" :key="k" :value ="f">{{f}}</option>
                    </select> -->
                </div>
                <h3 style="clear:both; text-align:center;">公会任务 {{grade}} 阶</h3>
                <table border="1">
                    <thead>
                        <th>任务</th>
                        <!-- <th>NPC</th> -->
                        <th>缴交物品</th>
                        <th>任务总次数</th>
                        <th>任务奖励</th>
                        <th>出现概率</th>
                    </thead>
                        <tr v-for="(v,k) in result.list" :key="k">
                            <td>
                                <div class="flex-mission">
                                    <div>
                                        <npc-icon :IconId="v.npcIcon"></npc-icon>
                                    </div>
                                    <div class="mission-content">
                                        <p>{{v.name}}</p>
                                        <p>{{v.subname}}</p>
                                    </div>
                                </div>
                            </td>
                            <td>
                                <item-icon-tooltip
                                    :itemId="v.itemId"
                                    :itemStub="v.item"
                                    :count="v.itemCount"
                                    :noLink="noLink">
                                </item-icon-tooltip>
                            </td>
                            <td>{{v.missionCount}}</td>
                            <td>
                                <div class="flex-mission">
                                    <div>
                                        次数内
                                        <div class="Title">
                                            <div v-for="(vv,kk) in v.startReward" :key="kk" class="titleitem">
                                                <item-icon-tooltip
                                                    :itemId="vv.itemId" 
                                                    :itemStub="vv.item"
                                                    :count="vv.count"
                                                    :noLink="noLink">
                                                </item-icon-tooltip>
                                            </div>
                                        </div>
                                    </div>
                                    <div>
                                        次数后
                                        <div class="Title">
                                            <div v-for="(vv,kk) in v.endReward" :key="kk" class="titleitem">
                                                <item-icon-tooltip
                                                    :itemId="vv.itemId" 
                                                    :itemStub="vv.item"
                                                    :count="vv.count"
                                                    :noLink="noLink">
                                                </item-icon-tooltip>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </td>
                            <td>{{v.ratio | rate}}%</td>
                        </tr>
                </table>
            </div>
            <pager :pageSize="pageSize" v-model="pageNo" @on-jump="jump" style="float:right;"></pager>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import pager from '@/components/util/Pager';
import NpcIcon from "@/components/NPC/NpcIcon"
import selector from "@/components/util/selector"
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        pager,
        NpcIcon,
        selector
    },
    data(){
        return{
            result:{
                list:null,
                count:null,
            },
            noLink:false,
            loading:true,
            grade:1,
            pageSize: 30,
            pageNo: 1,
            cacheArray:[],
        }
    },
    methods:{
        jump(id){
            this.pageNo = id;
            this.fetchData();
        },
        fetchData(){
            const cacheKey = this.grade + '-' + this.pageNo;
            if(this.cacheArray[cacheKey]){
                this.result = this.cacheArray[cacheKey];
                this.loading = false;
            }else{
            this.loading = true;
            GameInfo.GuildMission(this.grade,this.pageNo,this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.pageSize = Math.floor(res.count/15) + 1;
                        this.loading = false;
                        this.cacheArray[cacheKey] = res;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
        watchChange(e){
            this.grade = e.target.value;
            this.pageNo = 1;
            this.fetchData();
        },
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.stats {
    //overflow-x: auto;
    //overflow-y:hidden;
    max-width: 250px;
    @media only screen and (min-width: 450px) {
        max-width: 380px;
    }

    @media only screen and (min-width: 550px) {
        max-width: 500px;
    }

    @media only screen and (min-width:650px) {
        max-width: 400px;
    }

    @media only screen and (min-width:780px) {
        max-width: 500px;
    }

    @media only screen and (min-width:900px) {
        max-width: 644px;
    }

    @media only screen and (min-width:@min-desktop-wide-width) {
        max-width: 950px;
    }
}

.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.flex-mission{
    display:flex;
    align-items: center;
    //justify-content:space-between;
}

.mission-content {
    margin: 0 20px;
    p {
        text-align: start;
    }
}
</style>
 