<template>
<div>
  <label class="select" for="slct">
    <select id="slct" required="required" @change="watchChange" v-model="bindValue">
      <option class="op-text" value="" disabled="disabled" selected="selected"></option>
      <option class="op-text" v-for="(f,k) in options" :key="k" :value ="f">
        <template v-if="isFloor">{{ f | floorName }}</template>
        <template v-else>{{f}}</template>
      </option>
    </select>
    <svg>
      <use xlink:href="#select-arrow-down"></use>
    </svg>
  </label>
  <!-- SVG Sprites-->
  <svg class="sprites">
    <symbol id="select-arrow-down" viewbox="0 0 10 6">
      <polyline points="1 1 5 5 9 1"></polyline>
    </symbol>
  </svg>
</div>
</template>
<script>
export default {
  //props:["options","target","isFloor"],
  props:{
    options:{
      type: Array,
      require:true,
    },
    target:{
      require:true,
    },
    isFloor:{
      type: Boolean,
      default:false,
    }
  },
  data(){
    return{
      bindValue: this.target
    }
  },
  watch:{
    target(){
      this.$set( this, 'bindValue', this.target )
    }
  },
  methods:{
    watchChange(e){
      this.$emit('change',e);
    }
  },
  filters:{
    floorName: function (value) {
        switch (value) {
            case 4:
                return '深渊';
            case 3:
                return '大师';
            case 2:
                return '困难';
            case 1:
                return '普通';
            case 0:
                return '简单';
            default:
                return value-4 ;
        }
    }
},
  
}
</script>
<style lang="less">
.select {
  position: relative;
  min-width: 200px;
}
.select svg {
  position: absolute;
  right: 12px;
  top: calc(50% - 3px);
  width: 10px;
  height: 6px;
  stroke-width: 2px;
  stroke: #A6AEB3;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  pointer-events: none;
}
.select select {
  -webkit-appearance: none;
  padding: 7px 40px 7px 12px;
  border-radius: 5px;
  //box-shadow: 0 1px 3px -2px #9098a9;
  cursor: pointer;
  font-family: inherit;
  transition: all 150ms ease;
  background-color: #3b6e9675;
  //padding: 12px;
  width: 150px;
  border: none;
  font-size: 20px;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
  //outline: none;
  color:white;
  .op-text{
    background-color: #4488AA;
    
  }
}
.select select:required:invalid {
  color: white;
}
.select select option {
  color: white;
}
.select select option[value=""][disabled] {
  display: none;
}
.select select:focus {
  outline: none;
  //border-color: #5AA9E5;
  //box-shadow: 0 0 0 2px rgba(0,119,255,0.2);
}
.select select:hover + svg {
  stroke: #98BCD4;
}
.sprites {
  position: absolute;
  width: 0;
  height: 0;
  pointer-events: none;
  user-select: none;
}

</style>