<template>
    <div>
        <h1 class="divided">公会内容</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div class="section">
                <div class="title">常规内容</div>
                <h4>{{result.content.name}}</h4>
                {{result.content.tooltip}}
                期限:{{result.content._Period}}天
                <table>
                    <thead>
                        <th>种类</th>
                        <th>排名</th>
                        <th>奖励</th>
                    </thead>
                        <tr v-for="(v,k) in result.rank" :key="k">
                            <td>{{v.typeName}}</td>
                            <td v-if="v._StartRank == v._EndRank">{{v._StartRank}}</td>
                            <td v-else>{{v._StartRank}} ~ {{v._EndRank}}</td>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.items" :key="kk" class="titleitem">
                                        <item-icon-tooltip
                                            :itemId="vv.itemId" 
                                            :itemStub="vv.item"
                                            :count="vv.count"
                                            :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                        </tr>
                </table>
            </div>
            <div class="section">
                <div class="title">每天签到奖励</div>
                <table>
                    <thead>
                        <th>类型</th>
                        <th>捐币人数</th>
                        <th>奖励</th>
                    </thead>
                        <tr v-for="(v,k) in result.attendance" :key="k">
                            <td>{{v.RowId}}</td>
                            <td>{{v._StartCount}} ~ {{v._EndCount}}</td>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.items" :key="kk" class="titleitem">
                                        <item-icon-tooltip
                                            :itemId="vv.itemId" 
                                            :itemStub="vv.item"
                                            :count="vv.count"
                                            :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                        </tr>
                </table>
            </div>
            <div class="section">
                <div class="title">伤害奖励</div>
                <table>
                    <thead>
                        <th>阶段</th>
                        <th>伤害计数</th>
                        <th>奖励</th>
                    </thead>
                        <tr v-for="(v,k) in result.damageReward" :key="k">
                            <td>{{v._Step}}</td>
                            <td>{{v._GuildTotalDamage}} E</td>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.items" :key="kk" class="titleitem">
                                        <item-icon-tooltip
                                            :itemId="vv.itemId" 
                                            :itemStub="vv.item"
                                            :count="vv.count"
                                            :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                        </tr>
                </table>
            </div>
            <div class="section">
                <div class="title">公会商店</div>
                    <div style="display:flex;justify-content:space-evenly;">
                        <shop-browser :shopId="shopId1"  :useClick="useClick" />
                        <shop-browser :shopId="shopId2"  :useClick="useClick" />
                    </div>
                </div>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import ShopBrowser from '@/components/item/shop/ShopBrowser.vue';
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        ShopBrowser
    },
    data(){
        return{
            result:{
                rank:null,
                content:null,
                attendance: null,
                damageReward:null,
            },
            noLink:false,
            loading:true,
            shopId1:97001,
            useClick: true,
            shopId2:320000,
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.GuildContentCache){
                this.result = GameInfo.GuildContentCache;
                this.loading = false;
            }else{
            GameInfo.GuildContent(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.section {
        position: relative;
        margin-top: 35px;
        border-top: 1px solid @dv-c-accent-1;
        padding-top: 4px;
        .title {
            font-size: 12px;
            color: @dv-c-accent-1;
            text-transform: uppercase;
            letter-spacing: 0.2em;
            position: absolute;
            top: -1.3em;
            .left(0);
            user-select: none;
        }
        table{
            margin: 0 auto;
        }
}

.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}
</style>