<template>
    <div>
        <h1 class="divided">公会等级</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <h3>等级信息</h3>
            <div class="notice-title-guild">
            <div>注：</div>
            <div>创建公会需花费{{result.level[0]._ReqGold | gold}}</div>
            <div>公会福利是继承前面等级的</div>
            </div>
            <table border="1">
                <thead>
                    <th>公会等级</th>
                    <th>升级点数</th>
                    <th>公会福利</th>
                </thead>
                    <tr v-for="(v,k) in result.level" :key="k">
                        <td>{{v._GuildLevel}}</td>
                        <td>{{v._ReqGuildPoint}}</td>
                        <td>
                            <div class="Title">
                                <div v-for="(vv,kk) in v.rewardInfo" :key="kk" class="titleitem" style="margin:5px 10px">
                                    <item-icon-tooltip
                                        :itemId="vv.itemId" 
                                        :itemStub="vv.item"
                                        :count="1"
                                        :noLink="noLink">
                                    </item-icon-tooltip>
                                    <span v-if="vv.guildPoint>0">需{{vv.guildPoint}}公会点</span>
                                    <span v-if="vv.gold>0">需要{{vv.gold | gold}}</span>
                                    <span v-if="vv.period>0">有{{vv.period}}天期限</span>
                                </div>
                            </div>
                        </td>
                    </tr>
            </table>

            <h3>声望信息</h3>
            <table border="1">
                <thead>
                    <th>声望等级</th>
                    <th>描述</th>
                    <th>排名</th>
                    <th>声望奖励</th>
                </thead>
                <tr v-for="(v,k) in result.repute" :key="k">
                    <td>{{v.name}}</td>
                    <td v-html="v.desc"></td>
                    <td v-if="v._StartRank == v._EndRank && v._StartRank != 0">第 {{v._StartRank}} 名</td>
                    <td v-else-if="v._StartRank != 0">第 {{v._StartRank}} ~ {{v._EndRank}} 名</td>
                    <td v-else>排名之外</td>
                    <td>
                        <div class="repute-Title">
                            <div v-for="(vv,kk) in v.items" :key="kk" class="repute-titleitem">
                                <item-icon-tooltip
                                    :itemId="vv.itemId" 
                                    :itemStub="vv.item"
                                    :count="vv.count"
                                    :noLink="noLink">
                                </item-icon-tooltip>
                            </div>
                        </div>
                    </td>

                </tr>
            </table>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip
    },
    data(){
        return{
            result:{
                level:null,
                repute:null,
            },
            noLink:false,
            loading:true,
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.GuildInfoCache){
                this.result = GameInfo.GuildInfoCache;
                this.loading = false;
            }else{
            GameInfo.GuildInfo(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        }
    }
}
</script>
<style lang="less" scoped>
.Title {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .titleitem {
        flex: 0 0 auto;
        display: flex;
        flex-direction: row;
        width: 200px;
        border: 1px solid #98BCD4;
        span {
            margin: auto;
            width: 140px;
        }
    }
}

.repute-Title {
    display: flex;
    flex-direction: row;

    .repute-titleitem {
        flex: 0 0 auto;
    }
}

.notice-title-guild >div{
    margin-bottom:0.5rem;
}
</style>